export default {
	data() {
		return {
			pH:0, //窗口高度
			navHeight:0, //元素的所需高度
			cartList:[],//商品列表
			allPrice:0.00,//总价
			checked:false,//单选框选中属性
			allchecked:false
		}
	},
	onLoad() {

	},
	onReady() {
		let that=this;
			uni.getSystemInfo({ //调用uni-app接口获取屏幕高度
				success(res) { //成功回调函数
					that._data.pH=res.windowHeight //windoHeight为窗口高度，主要使用的是这个
					let titleH=uni.createSelectorQuery().select(".scroll"); //想要获取高度的元素名（class/id）
					titleH.boundingClientRect(data=>{
						let pH=that._data.pH; 
						that._data.navHeight=pH-data.top  //计算高度：元素高度=窗口高度-元素距离顶部的距离（data.top）
					}).exec()
				}
			})
	
	},
	onShow() {
		console.log(this.cartList);
		this.countAllPrice();//计算总价
		
		//购物车页面获取登录状态数据
		try{
			let userInfo=uni.getStorageSync('userInfo');
			console.log(userInfo);
			if(userInfo){
				uni.request({




					url: 'http://localhost:3000/user/login',

				    data: {
						userInfo
					},
				    method:'POST',
				}).then(res=>{
					this.cartList=this.$store.state.cartList;
				})
			}else{
				uni.showModal({
					title: '提示',
					content: '请先登录',
					showCancel:false,
					success: ()=>{
						uni.switchTab({
							url: '../user/user'
						});
					}
				})
			}
		}catch(e){
			//TODO handle the exception
		}
		
	
		
	},
	methods: {
		// add(index){
		// 	this.cartList[index].num++;
		// 	this.$set(this.cartList,index,this.cartList[index]);
		// },
		// sub(index){
		// 	this.cartList[index].num--;
		// 	this.$set(this.cartList,index,this.cartList[index]);
		// 	if(this.cartList[index].num<=0){
		// 		this.cartList.splice(index,1);
		// 	}
		// }
		//商品数量增减
		calculate(bool,index){
			if(bool){
				this.cartList[index].num++;
			}else {
				this.cartList[index].num--;
			}
			if(this.cartList[index].num<=0){
				this.cartList.splice(index,1);
			}else {
				this.$set(this.cartList,index,this.cartList[index]);
			}
			this.countAllPrice();
		},
		//总价
		countAllPrice(){
			let allPrice=0;
			this.cartList.map((item)=>{
				if(item.flag)
				allPrice+=item.num*item.Price
			})
			this.allPrice=allPrice.toFixed(2);
		},
		//单选
		changeitem(item){
			// console.log(item);
			item.flag=!item.flag;
			if(item.flag){
				let test =this.cartList.every(item=>{
					return item.flag===true
				})
				if(test){
					this.allchecked = true
				} else {
					this.allchecked = false
				}
			}else{
				this.allchecked = false
			}
			this.countAllPrice();
		},
		allchange() {
			this.allchecked = !this.allchecked
			// 如果 allcheckked 为真  全选被选中时，那么设置每一项都被选中
			if(this.allchecked) {
				this.cartList.map(item => {
					item.flag = true
				})
			}else {
				this.cartList.map(item => {
					item.flag = false
				})
			}
			this.countAllPrice();
		},
	}
}
